.treeMapWrapper {
  width: 100%;
  height: 100%;
}
.treeMapWrapper #chart {
    position: relative;
    width: 100%;
    height: 100%;
}
.treeMapWrapper .cell {
    position: absolute;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
}
.treeMapWrapper .cell.is-parent {
      justify-content: flex-start;
      align-items: flex-start;
}
.treeMapWrapper .cell.hide {
      opacity: 0;
      pointer-events: none;
}
.treeMapWrapper .cell.hide .text {
        filter: blur(10px);
}
.treeMapWrapper .cell:hover .text {
      color: white;
}
.treeMapWrapper .cell.level-0 {
      z-index: 4;
      font-size: 15vmin;
      display: none;
}
.treeMapWrapper .cell.level-1 {
      z-index: 3;
      font-size: 10vmin;
}
.treeMapWrapper .cell.level-2 {
      z-index: 2;
      font-size: 5vmin;
}
.treeMapWrapper .cell.level-3 {
      z-index: 1;
      font-size: 2.5vmin;
}
.treeMapWrapper .cell .text {
      display: flex;
      justify-content: center;
      align-items: center;
}
.treeMapWrapper .cell .text span {
        margin: 0 5px;
        cursor: pointer;
}
.treeMapWrapper .cell .text .check {
        position: relative;
        display: inline-block;
        width: 1.4em;
        height: 1.4em;
        background: #fff;
        border-radius: 50%;
        vertical-align: middle;
}
.treeMapWrapper .cell .text .check.checked:after, .treeMapWrapper .cell .text .check:hover:after {
          border-color: #1e7ec0;
}
.treeMapWrapper .cell .text .check:after {
          content: '';
          position: absolute;
          width: 1.5em;
          height: 1em;
          border: solid 2px #afacac;
          border-top: none;
          border-right: none;
          transform: rotate(-55deg);
          top: -0.1em;
}
